.container{
    padding: 79px 20px;
    background-color:  #28A745;
    color: #FFFFFF;
}
.preTitle{
font-size: 16px;
line-height: 22px;
margin-bottom: 16px;
}
.title{
    font-size: 32px;
line-height: 44px;

margin-bottom: 24px;
font-weight: 400;

}
.text{
    font-size: 16px;
line-height: 22px;
margin-bottom: 24px;
}
.button{
    padding: 16px 32px;

    width: 146px;
    height: 54px;
    left: 20px;
    color: #FFFFFF;
    border: 1px solid #FFFFFF;
    border-radius: 5px;
background-color: transparent;
font-size: 16px;
line-height: 22px;
}
.button:hover{
    background: #F4F4F4;
    border: 1px solid #28A745;
    color: #28A745;
    transition: background 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
@media screen and (min-width: 768px){
   .preTitle{
    font-size: 18px;
line-height: 25px;
   }
   .title{
    font-size: 40px;
line-height: 48px;
   }
   .text{
    font-size: 18px;
line-height: 25px;
   }
   .button{
    font-size: 18px;
line-height: 25px;
width: 156px;
   }
  
   .container{
    padding: 62px 32px;
    width: 52%;
   }

}
@media screen and (min-width: 1360px){
    .container{ 
    padding: 80px 24px;}
}